import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../../assets/css/login.css';
import Logo from '../../assets/imagen/logito.png';

const Login: React.FC = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [loginMessage, setLoginMessage] = useState('');
  const navigate = useNavigate();

  const handleLogin = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    console.log('Formulario enviado con:', { username, password });

    const usuarios = JSON.parse(localStorage.getItem('usuarios') ?? '[]');
    console.log('Usuarios guardados:', usuarios); // Log para verificar usuarios guardados

    const usuario = usuarios.find((user: any) => {
      const isUsernameMatch = user.username.trim() === username.trim();
      const isPasswordMatch = user.contrasena.trim() === password.trim();
      console.log(`Comparando: ${user.username.trim()} === ${username.trim()} => ${isUsernameMatch}`);
      console.log(`Comparando: ${user.contrasena.trim()} === ${password.trim()} => ${isPasswordMatch}`);
      return isUsernameMatch && isPasswordMatch;
    });

    if (usuario) {
      // Usuario válido, establecer usuario actual en el almacenamiento de sesión
      sessionStorage.setItem('usuarioActual', JSON.stringify(usuario));
      console.log('Usuario encontrado:', usuario); // Log para verificar usuario encontrado
      // Redirigir a la página de perfil
      navigate('/perfil');
    } else {
      // Credenciales inválidas, mostrar mensaje de error
      setLoginMessage('Nombre de usuario o contraseña incorrectos.');
    }
  };

  return (
    <div className="login-container">
      <img src={Logo} alt="logo" className="logito" />
      <h2>Iniciar Sesión</h2>
      <form id="loginForm" onSubmit={handleLogin}>
        <input
          type="text"
          id="username"
          placeholder="Nombre de usuario"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          required
        />
        <input
          type="password"
          id="password"
          placeholder="Contraseña"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
        />
        <button type="submit">Iniciar Sesión</button>
      </form>
      <div className="btn-container">
        <button onClick={() => { navigate('/loginAdmin'); }}>Iniciar sesión como Administrador</button>
      </div>
      {loginMessage && <p id="loginMessage" className="error">{loginMessage}</p>}
    </div>
  );
};

export default Login;
